/*
//  
// () => {} vs function() {}

const double = function (x) {
  return x * 2;
};
console.log("double: ", double(7));

const doubleArrow = (x) => {
  return x * 2;
};
console.log("doubleArrow", doubleArrow(7));
*/

/*
// 
// IIFE, Immediately-Invoked Function Expression

const a = 7;
function double() {
  console.log(a * 2);
}
double();

(function () {
  console.log(a * 2);
})();
*/

/*
// (Hoisting)
//      

const a = 7;

double();

function double() {
  console.log(a * 2);
}
*/

